___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', monospace;
  font-weight: 400;
}

.icon {
  width: 30px;
  height: 30px;
  border-radius: 5px;
  cursor: pointer;
}
.icon:hover {
  background-color: #e7e2e2;
}

header {
  display: flex;
  gap: 50px;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
header h1 {
  font-size: 2.5rem;
  color: white;
  text-shadow: 3px 3px black;
  text-transform: uppercase;
  width: min-content;
  position: absolute;
  padding: 20px;
  z-index: 1;
  bottom: 10px;
}
header picture {
  position: relative;
}
header picture img {
  width: 480px;
  border-radius: 5px;
}
.about h3 {
  font-size: 3em;
  background-color: black;
  color: white;
  box-shadow: 5px 5px grey;
  width: max-content;
  position: relative;
  top: 20px;
  left: 30px;
  z-index: 1;
}
.about .text {
  font-size: 30px;
  color: grey;
  border: 0.1px solid grey;
  padding: 40px;
  max-width: 70vw;
  position: relative;
}
.about .links {
  position: absolute;
  right: 50px;
  margin: 5px;
}

main {
  padding: 2rem;
}
main h2 {
  font-size: 2.5rem;
  font-weight: bolder;
  text-align: center;
  margin-bottom: 2rem;
}
.projects {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  justify-content: center;
}
.project {
  width: 400px;
  height: 350px;
  padding: 50px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  box-shadow: 10px 10px 10px 5px #00000033;
  transition: 0.4s transform;
  background-color: rgb(247, 247, 247);
}
.project:hover {
  transform: scale(1.1);
}

.project h3 {
  font-weight: bold;
}
.project .screenshot {
  align-self: center;
  border-radius: 5px;
}
.project .screenshot img {
  width: 300px;
}
.project .links {
  align-self: flex-end;
  display: flex;
  gap: 10px;
  align-items: center;
  margin-top: 10px;
}

footer {
  padding: 40px;
  height: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #8c52ff;
  border-radius: 10px 10px 0px 0px;
}

footer .contact .text {
  font-size: 1.5rem;
  max-width: 45ch;
}

footer .contact {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

footer picture img {
  width: 400px;
  border-radius: 5px;
}

.phone,
.mail {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: bolder;
}

@media (max-width: 1100px) {
  header h1 {
    font-size: 2rem;
  }
  .about h3 {
    font-size: 2rem;
  }
  header picture img {
    width: 300px;
  }
  .about .text {
    font-size: 20px;
    padding: 40px;
  }

  footer picture img {
    width: 250px;
  }
}

@media (max-width: 710px) {
  header h1 {
    font-size: 1.5rem;
  }
  .about h3 {
    font-size: 1.5rem;
  }
  header picture img {
    width: 200px;
  }
  .about .text {
    font-size: 15px;
  }

  footer .contact .text {
    font-size: 1.2rem;
  }

  footer picture img {
    width: 150px;
  }
}

@media (max-width: 530px) {
  header {
    flex-direction: column;
  }
  header h1 {
    font-size: 1.5rem;
  }
  header picture img {
    width: 250px;
  }

  main h2 {
    font-size: 2rem;
  }
  .project {
    width: 250px;
    height: 300px;
    gap: 10px;
    padding: 20px;
  }
  .project .screenshot img {
    width: 200px;
  }

  footer {
    flex-direction: column;
    flex-flow: column-reverse;
    height: 500px;
  }
}
